.page-body {
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: "Ubuntu", "Arial", sans-serif;
  font-size: 16px;
  font-weight: normal;
  line-height: 16px;
  color: #000000;
}
.wrapper {
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .wrapper {
    width: 700px;
    margin: 0 auto;
  }
}
@media (min-width: 1280px) {
  .wrapper {
    width: 1180px;
    margin: 0 auto;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-family: "Ubuntu";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Ubuntu-Regular.woff2") format("woff2"), url("../fonts/Ubuntu-Regular.woff") format("woff");
}
@font-face {
  font-family: "Ubuntu";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Ubuntu-Medium.woff2") format("woff2"), url("../fonts/Ubuntu-Medium.woff") format("woff");
}
@font-face {
  font-family: "Ubuntu";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Ubuntu-Bold.woff2") format("woff2"), url("../fonts/Ubuntu-Bold.woff") format("woff");
}
.page-header {
  width: 100%;
  min-height: 50px;
  background-color: #9564AA;
  margin: 0;
  padding: 0;
}
@media (min-width: 1280px) {
  .page-header {
    min-height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.header-menu {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
}
.header-menu-wrapper--close .main-nav__list {
  display: none;
}
@media (min-width: 1280px) {
  .header-menu-wrapper--close .main-nav__list {
    display: flex;
  }
}
.page-header__logo {
  position: absolute;
  content: "";
  top: 7px;
  right: 0;
  width: 38px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 26px;
  line-height: 26px;
  font-weight: bold;
  color: #FFFFFF;
  text-decoration: none;
}
@media (min-width: 1280px) {
  .page-header__logo {
    display: block;
    position: relative;
    width: fit-content;
    height: fit-content;
  }
}
.page-header__logo--text {
  display: none;
}
@media (min-width: 1280px) {
  .page-header__logo--text {
    display: block;
    font-size: 48px;
    line-height: 48px;
    font-weight: bold;
  }
}
@media (min-width: 1280px) {
  .page-header__logo--image {
    display: none;
  }
}
.main-nav {
  position: relative;
}
.main-nav__toggle {
  position: absolute;
  content: "";
  top: 17px;
  left: 0;
  height: 2px;
  width: 23px;
  background-color: #FFFFFF;
  box-shadow: 0 6px 0 0 #FFFFFF, 0 12px 0 0 #FFFFFF;
  border: none;
  cursor: pointer;
}
@media (min-width: 1280px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 1280px) {
  .main-nav__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 15px;
  }
}
.main-nav__item {
  position: relative;
  padding: 6px 13px;
}
.main-nav__item--special {
  background-color: #684677;
  border-radius: 10px;
  padding-right: 40px;
}
.main-nav__item--special::after {
  position: absolute;
  content: "";
  top: 7px;
  right: 13px;
  width: 18px;
  height: 15px;
  background-image: url(../../img/icon-diamond.svg);
  background-repeat: no-repeat;
  background-size: 18px 15px;
  z-index: 2;
}
.main-nav__item-link {
  text-decoration: none;
  font-size: 16px;
  line-height: 16px;
  font-weight: bold;
  color: #FFFFFF;
}
.main-nav__item-link--special {
  color: #FFCF2D;
}
.main-nav__item-link:hover,
.main-nav__item-link:focus {
  text-decoration: underline;
}
.main-nav__item-link:active {
  color: rgba(255, 255, 255, 0.5);
}
.main-content {
  padding-top: 18px;
  padding-bottom: 28px;
}
@media (min-width: 1280px) {
  .main-content {
    padding-top: 30px;
  }
}
@media (min-width: 1280px) {
  .main-content__wrapper {
    display: flex;
    column-gap: 30px;
  }
}
.credit-offer {
  padding: 0;
  padding-bottom: 32px;
}
.credit-offer-title {
  font-size: 20px;
  line-height: 20px;
  font-weight: 700;
  color: #000000;
  padding: 0;
  margin: 0;
  margin-bottom: 35px;
  position: relative;
}
.credit-offer-title::after {
  position: absolute;
  content: "";
  bottom: -10px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: rgba(149, 100, 170, 0.2);
}
@media (min-width: 1280px) {
  .credit-offer-title {
    font-size: 40px;
    line-height: 40px;
    font-weight: 700;
    text-shadow: 4px 4px 0px rgba(0, 0, 0, 0.25);
  }
  .credit-offer-title::after {
    bottom: -20px;
  }
}
.limited-offer__offers .swiper-wrapper {
  width: auto;
}
.limited-offer__offers-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  width: 100%;
  height: auto;
}
@media (min-width: 1280px) {
  .limited-offer__offers-list {
    row-gap: 30px;
    flex-wrap: wrap;
    column-gap: 16px;
    padding: 15px 0;
    width: auto;
  }
}
.limited-offer__offers-list .limited-offer__offers-item {
  width: 260px;
}
@media (min-width: 768px) {
  .limited-offer__offers-list .limited-offer__offers-item {
    width: 680px;
  }
}
@media (min-width: 1280px) {
  .limited-offer__offers-list .limited-offer__offers-item {
    width: auto;
  }
}
.limited-offer__offers-item {
  position: relative;
  background-color: #9564AA;
  border-radius: 10px;
  text-align: center;
  padding: 16px 10px;
  margin-left: 20px;
  margin-bottom: 8px;
  flex-shrink: 0;
  width: 260px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .limited-offer__offers-item {
    margin-left: 20px;
    padding: 15px 50px;
  }
}
@media (min-width: 1280px) {
  .limited-offer__offers-item {
    padding: 15px;
    margin: 0;
    min-width: 150px;
    width: auto;
    background-color: rgba(149, 100, 170, 0.1);
    border: 2px solid #9564AA;
    align-self: stretch;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 11px;
    flex-grow: 1;
  }
}
.limited-offer__offers-times {
  position: absolute;
  content: "";
  top: 18px;
  left: -20px;
  z-index: 2;
  font-size: 14px;
  line-height: 14px;
  font-weight: 700;
  padding: 4px 7px;
  margin: 0;
  background-color: #FFCF2D;
  border-radius: 5px;
}
@media (min-width: 768px) {
  .limited-offer__offers-times {
    top: 18px;
    left: -20px;
  }
}
@media (min-width: 1280px) {
  .limited-offer__offers-times {
    display: none;
  }
}
.limited-offer__credit-amount {
  font-size: 24px;
  line-height: 24px;
  font-weight: 700;
  color: #FFFFFF;
  margin: 0;
  padding: 0;
  text-align: right;
}
.limited-offer__credit-amount--designation {
  font-size: 8px;
  line-height: 8px;
  font-weight: 700;
  vertical-align: middle;
  color: #000000;
  background-color: #FFCF2D;
  padding: 4px;
  border-radius: 50%;
}
@media (min-width: 1280px) {
  .limited-offer__credit-amount {
    color: #000000;
    text-align: center;
  }
  .limited-offer__credit-amount--dollars {
    display: none;
  }
}
.limited-offer__item-button {
  display: none;
}
@media (min-width: 1280px) {
  .limited-offer__item-button {
    display: block;
    font-size: 16px;
    line-height: 16px;
    font-weight: 500;
    text-align: center;
    padding: 17px 28px;
    border: none;
    border-radius: 5px;
    background-color: #FFFFFF;
    color: rgba(0, 0, 0, 0.2);
    cursor: pointer;
  }
}
@media (min-width: 1280px) {
  .limited-offer__item-button:hover {
    background-color: #FFCF2D;
    color: #000000;
  }
}
@media (min-width: 1280px) {
  .limited-offer__offers-item:has(.limited-offer__item-button:hover ) {
    background-color: #9564AA;
  }
}
@media (min-width: 1280px) {
  .limited-offer__credit-amount:has(+ .limited-offer__item-button:hover ) {
    color: #FFFFFF;
  }
}
.limited-offer__slider-toggle-list {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 8px;
}
.limited-offer__slider-toggle-list .swiper-pagination {
  position: relative;
}
@media (min-width: 1280px) {
  .limited-offer__slider-toggle-list {
    display: none;
  }
}
.swiper .swiper-pagination {
  position: relative;
  margin: 8px 0;
}
@media (min-width: 1280px) {
  .swiper .swiper-pagination {
    display: none;
  }
}
.limited-offer__slider-toggle-button {
  width: 7px;
  height: 7px;
  border: none;
  border-radius: 50%;
  padding: 0;
  margin: 0;
  background-color: rgba(255, 207, 45, 0.3);
  box-sizing: border-box;
}
.limited-offer__slider-toggle-button--active {
  background-color: #FFCF2D;
}
.limited-offer__button {
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
  color: #000000;
  text-align: center;
  padding: 20px;
  margin: 20px 0 15px;
  background-color: #FFCF2D;
  border: none;
  border-radius: 5px;
  width: 100%;
}
@media (min-width: 1280px) {
  .limited-offer__button {
    display: none;
  }
}
.limited-offer__timer {
  display: none;
}
@media (min-width: 1280px) {
  .limited-offer__timer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: nowrap;
    padding: 13px;
    border-radius: 10px;
    background-color: #C15CA1;
  }
}
.limited-offer__timer-title {
  font-size: 40px;
  line-height: 40px;
  font-weight: 700;
  color: #FFCF2D;
  margin: 0;
  -webkit-text-stroke: 1px #FFFFFF;
  text-stroke: 1px #FFFFFF;
}
.limited-offer__timer-comment {
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
  color: #FFFFFF;
}
.limited-offer__timer-timer {
  font-size: 40px;
  line-height: 40px;
  font-weight: 400;
  color: #000000;
  background-color: #FFFFFF;
  margin: 0;
  padding: 5px 20px;
  border-radius: 5px;
}
.limited-offer__agreement-checkbox {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 30px;
}
.limited-offer__agreement-checkbox--text {
  font-size: 10px;
  line-height: 16px;
  font-weight: 400;
  position: relative;
}
.limited-offer__agreement-checkbox--checkbox + .limited-offer__agreement-checkbox--text::before {
  content: "";
  position: absolute;
  left: -30px;
  top: 0;
  width: 20px;
  height: 20px;
  margin: 0;
  border: 1px solid #9564AA;
  border-radius: 5px;
  box-sizing: border-box;
  cursor: pointer;
}
.limited-offer__agreement-checkbox--checkbox:checked + .limited-offer__agreement-checkbox--text::after {
  content: "";
  position: absolute;
  left: -30px;
  top: 0;
  width: 20px;
  height: 20px;
  margin: 0;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: #9564AA;
  background-image: url(../img/check.svg);
  background-repeat: no-repeat;
  background-position: 5px 7px;
  background-size: 10px 6px;
}
.limited-offer__agreement-checkbox--link {
  text-decoration: none;
  color: #9564AA;
}
.other-functions {
  display: flex;
  flex-direction: column;
  gap: 13px;
}
@media (min-width: 1280px) {
  .other-functions {
    position: relative;
    padding-top: 120px;
    padding-bottom: 120px;
  }
}
.other-functions__logo-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.other-functions__logo {
  width: 81px;
  height: 67px;
}
@media (min-width: 1280px) {
  .other-functions__logo {
    width: 129px;
    height: 107px;
  }
}
.other-functions__functions-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
@media (min-width: 1280px) {
  .other-functions__functions-list {
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 129px;
    height: 107px;
  }
}
.other-functions__functions-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 9px;
}
.other-functions__functions-item--image {
  padding: 10px;
  border-radius: 50%;
  background-color: rgba(149, 100, 170, 0.1);
  overflow: visible;
}
.other-functions__functions-item--text {
  font-size: 12px;
  line-height: 12px;
  font-weight: 700;
  color: #9564AA;
  margin: 0;
}
@media (min-width: 1280px) {
  .other-functions__functions-item {
    position: absolute;
    max-width: 110px;
    flex-direction: column;
  }
  .other-functions__functions-item--text {
    text-align: center;
  }
}
@media (min-width: 1280px) {
  .other-functions__functions-item:first-child {
    top: -100px;
    left: -50px;
  }
}
@media (min-width: 1280px) {
  .other-functions__functions-item:nth-child(2) {
    top: -100px;
    right: -50px;
  }
}
@media (min-width: 1280px) {
  .other-functions__functions-item:nth-child(3) {
    top: 0px;
    right: -120px;
  }
}
@media (min-width: 1280px) {
  .other-functions__functions-item:nth-child(4) {
    top: 0px;
    left: -120px;
  }
}
@media (min-width: 1280px) {
  .other-functions__functions-item:nth-child(5) {
    bottom: -100px;
    left: -50px;
  }
}
@media (min-width: 1280px) {
  .other-functions__functions-item:nth-child(6) {
    bottom: -100px;
    right: -50px;
  }
}
.user-menu {
  display: none;
}
@media (min-width: 1280px) {
  .user-menu {
    display: block;
    box-sizing: border-box;
    min-width: 270px;
    background-color: rgba(149, 100, 170, 0.1);
    border: 2px solid rgba(149, 100, 170, 0.2);
    border-radius: 10px;
    padding: 20px;
  }
}
.user-menu__user {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 15px;
  margin-bottom: 20px;
}
.user-menu__user::after {
  position: absolute;
  content: "";
  bottom: -20px;
  left: 0;
  height: 2px;
  width: 100%;
  background-color: rgba(149, 100, 170, 0.2);
}
.user-menu__user-avatar {
  border: 2px solid #FFFFFF;
  border-radius: 50%;
  width: 49px;
  height: 49px;
}
.user-menu__user-name {
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
  color: #000000;
  margin: 5px 0;
}
.user-menu__user-button {
  cursor: pointer;
  text-transform: uppercase;
  font-size: 10px;
  line-height: 10px;
  font-weight: 400;
  color: #FFFFFF;
  background-color: #9564AA;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
}
.user-menu__menu-list {
  list-style: none;
  margin: 0;
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 25px;
}
.user-menu__menu-item-link {
  text-decoration: none;
  font-size: 16px;
  line-height: 16px;
  font-weight: 400;
  color: #000000;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  column-gap: 30px;
}
.user-menu__menu-item-link:hover,
.user-menu__menu-item-link:focus {
  text-decoration: underline;
}
.user-menu__menu-item-link:active {
  color: rgba(0, 0, 0, 0.5);
}
.user-menu__menu-item__text {
  margin: 0;
}
